@import "../../../variables.scss";

.arrow {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background: $white;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    z-index: 100;
    border: 1px $gray solid;

    .iconArrow {
        position: absolute;

    }

    &:hover {
        box-shadow: 0 3px 9px rgba(0, 0, 0, 0.2);
    }

    &:active {
        box-shadow: none;
    }


}