@import "../../../variables.scss";

.contentPagination {
    display: flex;
    height: 2.25rem;
    width: 100%;
    justify-content: space-between;
    margin: auto;
    margin-bottom: 3.5rem;
    max-width: 300px;

    .iconContent {
        width: 2.25rem;

        .icon {
            margin: .75rem;

            &:hover {
                color: $black;
            }

            &:active {
                color: $orange;
            }

        }
    }

    .numContent {
        color: $mediumGray;
        padding: .4rem .79rem;
        font-family: "neueBold";

        &:hover {
            color: $black;
        }

        &:active {
            color: $orange;
        }

        &.active {
            border-radius: 12px;
            box-shadow: inset 0 0 0 2px $orange;
            color: $black;

        }
    }
}